/**
 * Layout component that queries for data
 * with Gatsby's StaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import { Link } from "gatsby"
import { StaticQuery, graphql } from "gatsby"
import classNames from "classnames"

import Icon from "./Icon"

import "../css/Header.css"

const Header = ({ subTitle }) => (
  <StaticQuery
    query={graphql`
      query SiteTitleQuery {
        site {
          siteMetadata {
            title
            subTitle
            menuLinks {
              name
              link
            }
          }
        }
      }
    `}
    render={data => (
      <header className={classNames( 'row', 'siteWidth' )}>
        <Link className={classNames( 'icon' )} to='/' >
          <Icon />
        </Link>
        <div style={{ width:'100%', display: 'flex', flexDirection: 'column', justifyContent:'space-between' }}>
          <div className={classNames( 'aboveTitle' )} />
          <div className={classNames( 'row', 'spaceAround' )}>
            <div className={classNames( 'row', 'title' )}>
              <h>
                <Link to="/">
                  {data.site.siteMetadata.title}
                </Link>
              </h>
            </div>
            <div className={classNames( 'row', 'subTitle' )}>
              <h>
                {subTitle[0] || data.site.siteMetadata.subTitle[0]}&nbsp;
              </h>
              <h>
                {subTitle[1] || data.site.siteMetadata.subTitle[1]}
              </h>
            </div>
            <div />
            <div />
          </div>
          <div className={classNames( 'row' )}>
            <div style={{ flexGrow:1 }}/>
            <nav>
              {
                data.site.siteMetadata.menuLinks.map(link =>
                  <li key={link.name}>
                    <Link to={link.link}>
                      {link.name}
                    </Link>
                  </li>)
              }
            </nav>
            <div style={{ flexGrow:9.1 }}/>
          </div>
        </div>
      </header>
    )}
  />
)

Header.propTypes = {
  subTitle: [PropTypes.String],
}

Header.defaultProps = {
  subTitle: ['', ''],
}

export default Header
